
.grid_container {
    overflow: hidden;
    padding-top: 10px;
    padding-bottom: 95px;
    padding-left: 30px;
    padding-right: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 30% 30% 30%;
    grid-gap: 50px;
    text-align: center;
}


