
 footer {
    clear: both;
    width: 100%;
    height: 45px;
    background-color: #181e2c;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    text-align: center;
}

footer i {
    color: #ffffff;   
}

footer a {
    height: 100%;
    width: 55px;
    background-color: #1f2637;    
}

.footer_help i {
    padding-top: 15px;
}

.footer_settings {
    width: 55px;
    padding-top: 15px;
    border-bottom-right-radius: 5px;
}



